import { Fragment, SyntheticEvent, useState } from "react";
import { HEX } from "react-canvas-color-picker";
import { MoreVert, Search } from "@mui/icons-material";
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
  InputAdornment,
  List,
  ListItem,
  ListItemButton,
  Tab,
  TextField,
} from "@mui/material";
import { TabContext, TabList, TabPanel } from "@mui/lab";

import { StatusManagerTab } from "Shared/Enums/EngageLeads";
import {
  FlexRow,
  GrowColumn,
  ShrinkColumn,
} from "SharedComponents/CommonComponents";
import { useChatApi, useChatData } from "Contexts/Engage";
import { Delete, EditO } from "Assets/Icons";

import ColorSelector from "./ColorSelector";

const StatusManager = () => {
  const {
    showStatusManage,
    // NoteStatuses
  } = useChatData();
  const {
    setShowStatusManage,
    // setNoteStatuses
  } = useChatApi();
  const [currentTab, setCurrentTab] = useState<StatusManagerTab>(
    StatusManagerTab.NEW
  );
  const [colorButtonEl, setColorButtonEl] = useState<null | HTMLElement>(null);
  const [color, setColor] = useState<HEX>("#FF4949");
  const changeCurrentTab = (
    _event: SyntheticEvent,
    newTab: StatusManagerTab
  ) => {
    setCurrentTab(newTab);
  };

  return (
    <TabContext value={currentTab}>
      <Dialog
        open={showStatusManage}
        onClose={() => setShowStatusManage(false)}
        PaperProps={{ sx: { maxWidth: 320 } }}
        fullWidth
      >
        <DialogTitle
          component="div"
          sx={{
            borderBottom: "1px solid #24272C1A",
            py: 0,
            px: 1.5,
            mb: 2.5,
          }}
        >
          <TabList
            orientation="horizontal"
            variant="standard"
            onChange={changeCurrentTab}
            sx={{
              minHeight: "unset",

              "& .MuiTabs-flexContainer": {
                columnGap: 2.5,
                justifyContent: "center",
              },

              "& .MuiTabs-indicator": {
                height: 4,
              },

              "& .MuiTab-root": {
                color: "#24272C",
                fontSize: 18,
                fontWeight: 500,
                lineHeight: "21px",
                height: "auto",
                minHeight: "unset",
                textTransform: "capitalize",
                px: 0.5,
                py: 2,

                "&.Mui-selected": {
                  color: "inherit",
                },
              },
            }}
          >
            <Tab label="New Status" value={StatusManagerTab.NEW} />
            <Tab label="Manage Statuses" value={StatusManagerTab.MANAGE} />
          </TabList>
        </DialogTitle>
        <DialogContent sx={{ pb: 2.5, px: 1.5 }}>
          <TabPanel value={StatusManagerTab.NEW} sx={{ p: 0 }}>
            <TextField
              value={`Color: ${color}`}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Button
                      variant="text"
                      sx={{
                        bgcolor: color,
                        border: "1px solid #B0B0B0",
                        height: 38,
                        minWidth: "unset",
                        p: 0,
                        width: 38,
                        cursor: "pointer",

                        "&:hover": {
                          bgcolor: color,
                        },
                      }}
                      onClick={(event: React.MouseEvent<HTMLElement>) => {
                        setColorButtonEl(event.currentTarget);
                      }}
                    />
                  </InputAdornment>
                ),
                readOnly: true,
              }}
              inputProps={{
                sx: {
                  cursor: "default",
                  px: 1.5,
                },
              }}
              variant="standard"
            />
            <TextField label="Status Title" placeholder="Status Title" />
          </TabPanel>
          <TabPanel value={StatusManagerTab.MANAGE} sx={{ p: 0 }}>
            <FlexRow sx={{ mb: 1.25 }}>
              <GrowColumn sx={{ alignSelf: "center" }}>
                4 Status Available
              </GrowColumn>
              <ShrinkColumn>
                <IconButton>
                  <Search />
                </IconButton>
              </ShrinkColumn>
            </FlexRow>
            <List>
              <ListItem
                secondaryAction={
                  <Fragment>
                    <IconButton>
                      <EditO sx={{ color: "#24272C80" }} />
                    </IconButton>
                    <IconButton>
                      <Delete sx={{ color: "#24272C80" }} />
                    </IconButton>
                  </Fragment>
                }
                disablePadding
              >
                <ListItemButton
                  sx={{
                    alignItems: "center",
                    bgcolor: "#F2F3F7",
                    borderRadius: 2,
                    color: "#24272C",
                    columnGap: 1.5,
                    fontSize: 14,
                    fontWeight: 400,
                    height: 52,
                    lineHeight: "16px",
                    p: 0,

                    "&:hover": {
                      bgcolor: "#F2F3F7",
                    },
                  }}
                >
                  <MoreVert sx={{ color: "#24272C4D" }} />
                  <Box
                    component="span"
                    sx={{
                      bgcolor: "#DDE3FE",
                      borderRadius: 1,
                      height: 24,
                      minWidth: "unset",
                      p: 0,
                      width: 24,
                      cursor: "pointer",

                      "&:hover": {
                        bgcolor: "#DDE3FE",
                      },
                    }}
                  />
                  No Response
                </ListItemButton>
              </ListItem>
            </List>
          </TabPanel>
        </DialogContent>
        <DialogActions
          sx={{
            borderTop: "1px solid #24272C1A",
            justifyContent: "flex-start",
          }}
        >
          <Button
            sx={{
              fontSize: 14,
              fontWeight: 500,
              height: 40,
              lineHeight: "16px",
            }}
          >
            Save Status
          </Button>
        </DialogActions>
      </Dialog>
      <ColorSelector
        open={Boolean(colorButtonEl)}
        color={color}
        anchorEl={colorButtonEl}
        doClose={() => setColorButtonEl(null)}
        changeColor={setColor}
      />
    </TabContext>
  );
};

export default StatusManager;
